@media screen and (max-width: 1200px) {
  .hi-i-am {
    display: none;
  }
  .chandrahassan-jb {
    padding-top: 25px;
  }
  .name-description {
    opacity: 1;
  }
  .name-description.animate {
    animation: 1s ease 0s 1 normal forwards scale-down-center;
  }
  @keyframes scale-down-center {
    0% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
    to {
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }
  }
  .card1-parent,
  .sec-01 {
    flex-direction: column;
  }
}
@media screen and (max-width: 768px) {
  .navbar-links {
    display: none;
  }
  .cihamburger {
    display: flex;
  }
  .logoshtml-5-parent,
  .logostailwindcss-icon-parent {
    margin-left: -8%;
    padding-right: 5%;
  }
}
